.performance {
    margin: 100px 0 0 0;
    outline: 0px solid red;
    .goToPerformance {
        margin: 0 0 100px 0;
        height: 1px;
    }
    h1 {
        margin: auto;
        outline: 0px solid red;
        width: 70%;
        font-size: 50px;
        line-height: 70px;
        text-align: center;
    }
    .recommendationBlocks {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
